import ListContainer from '@/components/Common/ListContainer'
import CourseOrderCard from '@/components/Common/CourseOrderCard'
import { getTradeList, payTrade, cancleTrade } from '@/api/studyCenter'
export default {
  components: {
    ListContainer,
    CourseOrderCard
  },
  data () {
    return {
      listFlag: 0,
      loading: false,
      finished: false,
      tableData: [],
      pagination: {
        page: 1
      },
    }
  },
  mounted () {
    this.getListHand()
  },
  methods: {
    async getListHand () {
      const params = {
        ...this.pagination
      }
      const { data } = await getTradeList(params)
      this.loading = false
      if (data.code === 0) {
        this.tableData = this.pagination.page === 1 ? data.data.entries : this.tableData.concat(data.data.entries)
        if (!this.tableData.length && this.pagination.page === 1) {
          this.listFlag = 2
        } else {
          this.listFlag = 1
          this.pagination.page = this.pagination.page + 1
        }
        if (this.tableData.length >= +(data.data.total)) {
          this.finished = true
        }
      }
    },
    //支付订单
    async payOrder(item) {
      const { data } = await payTrade(item.tradeNO)
      if(data.code === 0){
        window.location.href = data.data
      }
    },
    //取消订单
    cancleOrder(item) {
      this.$dialog.confirm({
        message: '是否确认取消订单？',
      }).then( async () =>{
        const { data } = await cancleTrade(item.tradeNO)
        if(data.code === 0){
          this.$toast.success('取消成功!')
          this.pagination.page = 1
          this.getListHand()
        }
      }).catch(()=>{})
    }
  },
  head () {
    return {
      title: '课程订单—艾思云课堂'
    }
  }
}
